<template>
  <div class="container" id="indexPage">
    <div class="header-box flex-center">
      <div class="side-left flex-center">
        <img src="~@/assets/images/back@2x.png" alt="">
      </div>
      <van-tabs v-model:active="tabsActive" @click="tabClick" type="card" class="flex-item middle">
        <!--  -->
        <van-tab title="" name="1"></van-tab>
        <!--  -->
        <van-tab title="" name="2"></van-tab>
      </van-tabs>
      <div class="side-right flex-center" @click="$router.push({name: 'Share'})">
        <img src="~@/assets/images/share.png" alt="">
      </div>
    </div>
    <div class="top-tip flex-middle">
      <van-swipe style="height: 30px;" :autoplay="3000" vertical :show-indicators="false">
        <van-swipe-item class="row" v-for="item in records">
          {{item.investor}}{{item.income}}BUSD{{item.windIncome}}wind token
        </van-swipe-item>
      </van-swipe>
    </div>
    <!--  -->
    <template v-if="tabsActive === '1'">
      <index-option></index-option>
    </template>
    <!--  -->
    <template v-if="tabsActive === '2'">
      <index-pool></index-pool>
    </template>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch} from "vue";
import { useRoute, useRouter } from 'vue-router'
import IndexOption from "./IndexOption";
import IndexPool from "./IndexPool";
import BSNAbi from "@/utils/BSNAbi";
import tokenAbi from "@/utils/abi";
import BSNDataAbi from "@/utils/BSNDataAbi";
import config from "@/utils/config";
import {useStore} from "vuex";
export default {
   components: {
    IndexOption,
    IndexPool
  },
  setup () {
    const store = useStore()
    const route = useRoute()
    const router = useRouter()
    const data = reactive({
      selectIds: [],
      showSelect: false,
      checkboxGroup: null,
      checkAll:false,
      records: [],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      BsnContract: null,
      BusdContract: null,
      windTokenContract: null,
      BsnDataContract: null,
      tabsActive: route.query.name,
      tabClick: (name) => {
        // console.log(name)
        router.replace({
          name: 'Index',
          query: {
            name: name
          }
        })
      },
      initContract: () => {
        data.BsnContract = new data.web3.eth.Contract(BSNAbi, config.BsnAddress)
        data.BusdContract = new data.web3.eth.Contract(tokenAbi, config.BusdAddress)
        data.BsnDataContract = new data.web3.eth.Contract(BSNDataAbi, config.BsnDataAddress)
        data.windTokenContract = new data.web3.eth.Contract(tokenAbi, config.windToken)
        data.initData()
      },
      initData: () => {
        data.getList()
      },
      getList: async () => {
        let recordIds = await data.BsnDataContract.methods.getAllrecord().call();
        console.log(recordIds)
        let block = await data.web3.eth.getBlockNumber()
        let decimals = await data.BusdContract.methods.decimals().call();
        let windeciams = await data.windTokenContract.methods.decimals().call();
        recordIds.forEach(async (item) => {
          let order = await data.BsnDataContract.methods.getOrderById(item).call();
          let price = await data.BsnContract.methods.getBlockPrice(order.openBlock).call()
          let result = await data.BsnContract.methods.getWin(item).call()
          if (price > 0 &&  block >= order.openBlock && !order.isOpen) {
            order.canOpen = true;
          }
          if (price > 0 &&  block >= order.openBlock) {
            if (Number(result.back) > 0) {
              order.income = Number(result.back/Math.pow(10, decimals)).toFixed(2)
              order.windIncome = Number(result.winTokenAmount * 8/Math.pow(10, windeciams)/9).toFixed(2)
              order.isWin = true;
            } else {
              order.income = Number(result.back/Math.pow(10, windeciams)).toFixed(2)
              order.windIncome = Number(result.winTokenAmount * 8/Math.pow(10, windeciams)/9).toFixed(2)
              order.isWin = false;
            }
          }
          if (price <=0) {
            order.canOpen = false;
          }
          order.id = item;
          order.openPrice = price;
          order.investAmount =Number(order.investAmount / Math.pow(10, decimals)).toFixed(2)
          if (order.isWin) {
            console.log(order)
            data.records.unshift(order) 
          }
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less">
